import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from "react-bootstrap-icons";
import headerImg from "../assets/img/me_result.webp"

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ["BSc Majoring in Computer Science and Physics", "Software Engineer", "Data Scientist", "Swati-Mozambican", "Eswatini Citizen"];
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const period = 2000;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta)

        return () => {clearInterval(ticker)};
    }, [text])

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1)

        setText(updatedText);

        if (isDeleting) {
            setDelta(prevDelta => prevDelta / 2)
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true);
            setDelta(period);
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setDelta(500);
        }
    }

    // Scroll the window to Contact
    const scrollToContact = () => {
            window.scrollTo({
            top: document.documentElement.scrollHeight,
            behavior: 'smooth'
            })
    } 

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        {/* <span className="tagline">Welcome to my Portfolio</span> */}
                        <h1>{`Hi I'm Dominic Samo `}</h1>
                        <h2>
                            <span className="wrap">{text}</span>
                        </h2>
                        <p>
                          My interests are in Financial Technology (FinTech), Software Engineering (Android and Web App Development), Computational Physics and Data Science (Machine Learning and Deep Learning).
                          Completed my undegraduate degree at the University of Eswatini in the year 2021.
                        </p>
                          
                        <p>
                          I am a swimmer, cyclist, comic and graphic book artist, History, Political Science, World Geography, International Relations and Philosophy enthusiast.
                        </p>
                        <button onClick={scrollToContact}>Let's Connect <ArrowRightCircle size={25} /></button>
                    </Col>
                    <Col xs={12} md={6} xl={5}>
                        <img src={headerImg} alt="Header Img" />
                    </Col>
                </Row>
            </Container>
        </section>
    )
}